// import PropTypes from 'prop-types';
import React from "react";
// import axios from "axios"
import Movie from "./Movie";
import "./App.css";

class App extends React.Component {
  state = {
    isLoading: true,
    movies: []
  }; 
  getMovies = () => {
    const movies = [
      {
        id : 1,
        year : 2012,
        title : 'The Avengers',
        summary : "Marvel's The Avengers (classified under the name Marvel Avengers Assemble in the United Kingdom and Ireland), or simply The Avengers, is a 2012 American superhero film based on the Marvel Comics superhero team of the same name.",
        poster : 'https://upload.wikimedia.org/wikipedia/en/8/8a/The_Avengers_%282012_film%29_poster.jpg',
        genres: ['Action','SF']
      },
      {
        id : 2,
        year : 2015,
        title : 'Avengers:Age of Ultron',
        summary : 'Avengers: Age of Ultron is a 2015 American superhero film based on the Marvel Comics superhero team the Avengers.',
        poster : 'https://upload.wikimedia.org/wikipedia/en/f/ff/Avengers_Age_of_Ultron_poster.jpg',
        genres: ['Action','SF']
      },
      {
        id : 3,
        year : 2018,
        title : 'Avengers:Infinity War',
        summary : 'Avengers: Infinity War is a 2018 American superhero film based on the Marvel Comics superhero team the Avengers.',
        poster : 'https://upload.wikimedia.org/wikipedia/en/4/4d/Avengers_Infinity_War_poster.jpg',
        genres: ['Action','SF']
      },
      {
        id : 4,
        year : 2019,
        title : 'Avengers:Endgame',
        summary : 'Avengers: Endgame is a 2019 American superhero film based on the Marvel Comics superhero team the Avengers.',
        poster : 'https://upload.wikimedia.org/wikipedia/en/0/0d/Avengers_Endgame_poster.jpg',
        genres: ['Action','SF']
      }
    ]
    this.setState({movies:movies, isLoading: false})
  }
  
  componentDidMount(){
    this.getMovies();
  }
  render() {
    const {isLoading, movies} = this.state;
    return (
      <section className="container">
      {isLoading ? (
      <div className="loader">
        <span className="loader__text">Loading...</span>
      </div>) : (
      <div className="movies">
        {movies.map(movie => (
          <Movie key={movie.id} 
            id={movie.id} 
            year={movie.year} 
            title={movie.title} 
            summmary={movie.summary} 
            poster={movie.poster}
            genres={movie.genres}
          />
        ))}
      </div>
    )}
    </section>);
  }
}

export default App;